import classNames from 'classnames';
import React from 'react';
import { Form, Field } from 'react-final-form';
// import isDanger from './isDanger';
import renderControlInput from './renderControlInput';

const QuoteForm = ({ copy, notification, onClick, onSubmit }) => {
  return [
    notification && (
      <div
        className={classNames({ notification: true, 'is-success': true })}
        key="notification"
      >
        <button className="delete" onClick={onClick} />
        {copy['thanks-for-filling-out-quote-form']}
      </div>
    ),
    <Form
      initialValues={{
        key: 'quote',
        'length-of-project-hours': '',
        'length-of-project-minutes': '',
        'length-of-project-seconds': ''
      }}
      key="form"
      onSubmit={onSubmit}
      render={({ form, handleSubmit, submitting, values }) => (
        <form
          onSubmit={event => {
            event.preventDefault();
            handleSubmit(event).then(() => {
              form.reset();
            });
          }}
        >
          <div className="field">
            <label className="label">{copy['length-of-project']}</label>
          </div>
          <div className="field is-grouped is-grouped-multiline">
            <Field name="length-of-project-hours">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['length-of-project-hours-placeholder'],
                  submitting,
                  'number'
                )
              }
            </Field>
            <Field name="length-of-project-minutes">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['length-of-project-minutes-placeholder'],
                  submitting,
                  'number'
                )
              }
            </Field>
            <Field name="length-of-project-seconds">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['length-of-project-seconds-placeholder'],
                  submitting,
                  'number'
                )
              }
            </Field>
          </div>
          <div className="field">
            <div className="control">
              <button
                className={classNames({
                  button: true,
                  'is-link': true,
                  'is-loading': submitting
                })}
                disabled={submitting}
              >
                {copy['submit-order']}
              </button>
            </div>
          </div>
        </form>
      )}
      validate={values => {
        const errors = {};
        if (!values['length-of-project-hours']) {
          errors['length-of-project-hours'] = copy['required'];
        }
        if (!values['length-of-project-minutes']) {
          errors['length-of-project-minutes'] = copy['required'];
        }
        if (!values['length-of-project-seconds']) {
          errors['length-of-project-seconds'] = copy['required'];
        }
        return errors;
      }}
    />
  ];
};

export default QuoteForm;



